/* Styles for site forms */


//Nav Search Form styles
input[type="search"] {
    color: $color-black;
    background-color: $color-white;
    transition: all 0.3s ease;
    opacity: 0.25;

    &:focus {
        outline: none;
        box-shadow: 0 0 10px $color-gray;
        transition: opacity 0.5s 0.5s ease!important;
        opacity: 1;
    }
  }
  
// Custom gravity forms styles //

  //remove custom li dots
  .gfield_checkbox {
      
    li {
        &:before {
            content:none!important;
        }
    }
  }

  //begin custom form styles
  body {

    .gform_wrapper {

        .gform_body {

            .gfield_checkbox {
                column-count: 2;
    
                li {
                    
                    input[type=checkbox] {
                        opacity: 0;
                        position: absolute; 
    
                        +label {
    
                            position: relative;
                            overflow: hidden;
                            padding-left: 35px;
                            cursor: pointer;
                            display: inline-block;
    
                            &:before {
                                content: '';
                                position: absolute;
                                left: 0;
                                z-index: 1;
                                -webkit-transition: .2s;
                                transition: .2s;
                            }
    
                            &:after {
                                content: '';
                                position: absolute;
                                left: 0;
                                z-index: 1;
                                -webkit-transition: .2s;
                                transition: .2s;
                            }
                        }
                    }
                }
            }

            .gfield {
                margin:0;

                //styles for all inputs besides checkboxes
                .ginput_container_text, .ginput_container_phone, .ginput_container_email {

                    input {
                        margin: 3px 0;
                        padding: 8px 4px!important;
                        border: 1px solid $color-gray;
                        border-radius: 3px;
                        transition: .3s;

                        &:focus {
                            outline: none;
                            box-shadow: 0px 0px 4px $color-red;
                        }
                    }
                }  

                //styles for select inputs
                .ginput_container_select {

                    select {
                        padding: 10px 4px;
                        border: 1px solid $color-gray;
                        border-radius: 3px;

                        &:focus {
                            outline: none;
                            box-shadow: 0px 0px 4px $color-red;
                        }
                    }
                }

                .gfield_label {
                    padding: 15px 0 0;
                }
            }
        }

        .gform_footer {

            input[type=submit] {
                display: block;
                margin: 0 auto;
                color: $color-white;
                background-color: $color-red;
                border: $color-red;
                border-radius: 22px;
                padding: 10px 20px;
                cursor: pointer;
                transition: .3s;

                &:hover {
                    background-color: $color-gray;
                    transition: .3s;
                }
            }
        }
    }
  }  
      
  //styles not converted into sass yet but still functional

/* after */
body .gform_wrapper .gfield_checkbox li input[type=checkbox]:not(:checked) + label:after{
    width: 20px;
    height: 20px;
    border: 2px solid $color-black;
    top: 0px;
}
body .gform_wrapper .gfield_checkbox li input[type=checkbox]:checked + label:after{
    top: 0;
    width: 20px;
    height: 20px;
    border: 2px solid $color-red;
    background-color: $color-red;
    z-index: 0;
	border-radius: 2px
}
/* before */
body .gform_wrapper .gfield_checkbox li input[type=checkbox]:not(:checked) + label:before{
	    width: 0;
    height: 0;
    border: 3px solid transparent;
    left: 6px;
    top: 10px;
    -webkit-transform: rotateZ(37deg);
    transform: rotateZ(37deg);
    -webkit-transform-origin: 100% 100%;
    transform-origin: 100% 100%;
}
body .gform_wrapper .gfield_checkbox li input[type=checkbox]:checked + label:before{
	    top: 0;
    left: 1px;
    width: 8px;
    height: 13px;
	margin-top:3px;
    border-top: 2px solid transparent;
    border-left: 2px solid transparent;
    border-right: 2px solid $color-white;;
    border-bottom: 2px solid $color-white;;
    -webkit-transform: rotateZ(37deg);
    transform: rotateZ(37deg);
    -webkit-transform-origin: 100% 100%;
    transform-origin: 100% 100%;
}



